/* Global Styles */
body {
    font-family: 'Roboto', sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f4f6f9;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    justify-content: space-between;
  }
  
  /* Header Styling */
  .header {
    display: flex;
    justify-content: space-between;
    padding: 40px 20px;  /* Increased padding for larger height */
    background-color: #ffffff;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    position: sticky;
    top: 0;
    z-index: 1000;
  }
  
  .header img {
    height: 50px;  /* Adjust your logo size accordingly */
  }
  
  /* Centering Profile and Important Link Cards */
  .profile-section {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 30px;
    gap: 30px; /* Space between the cards */
  }
  
  /* Light Blue Background Behind the Cards */
  .profile-card, .important-link-card {
    width: 300px; /* Set fixed width for both cards to ensure same size */
    padding: 20px;
    background-color: #e3f2fd; /* Light blue background shade */
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  /* Profile Avatar */
  .profile-avatar {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background-color: #4caf50;
    color: white;
    font-size: 50px;
    position: relative;
    margin-bottom: 15px;
  }
  
  /* Green Tick on Avatar */
  .profile-avatar::after {
    content: '\2714'; /* Unicode check mark */
    position: absolute;
    right: -5px;
    top: -5px;
    background-color: #4caf50;
    color: white;
    border-radius: 50%;
    width: 25px;
    height: 25px;
    font-size: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  /* Important Link Section */
  .paper-importance {
    padding: 20px;
    background-color: #e3f2fd; /* Light blue background shade */
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .paper-importance a {
    color: #00796b;
    text-decoration: none;
  }
  
  .paper-importance a:hover {
    text-decoration: underline;
  }
  
  /* Section Styles (Project, Personal Details, Certifications) */
  .paper-section {
    padding: 20px;
    background-color: #fff;
    margin-top: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .project-form, .personal-form, .certifications-form {
    display: flex;
    flex-direction: column;
    gap: 15px;
    justify-content: center;
  }
  
  .MuiTextField-root {
    background-color: #f9f9f9;
    border-radius: 5px;
  }
  
  /* Buttons */
  button.MuiButton-root {
    background-color: #00796b;
    color: white;
    font-weight: 600;
    padding: 10px;
    margin-top: 10px;
    border-radius: 5px;
    cursor: pointer;
  }
  
  button.MuiButton-root:hover {
    background-color: #004d40;
  }
  
  /* Collapse Icons */
  .expand-open {
    transform: rotate(180deg);
    transition: transform 0.2s ease-in-out;
  }
  
  .expand-close {
    transform: rotate(0deg);
    transition: transform 0.2s ease-in-out;
  }
  
  /* General Card Styles */
  .MuiPaper-root {
    border-radius: 10px;
  }
  